@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);

.apple-cell{
    display: flex;
    width: 18px;
    height: 18px;
    background-color: rgb(160, 67, 67);
    border: 1px solid #9acc99;
    z-index: 1;
    position: relative;
}

.empty-cell{
    display: flex;
    width: 18px;
    height: 18px;
    border: 1px solid #9acc99;
    z-index: 1;
    position: relative;
}

.board-wrap{
    display:flex;
    width: 600px;
    height: 600px;
    flex-wrap: wrap;
    top: -600px;
    z-index: 0;
    position: relative;
}
.playPauseContainer{
    font-family: 'Press Start 2P', cursive;
    font-size: 80px;
    display:flex;
    width: 600px;
    height: 600px;
    background-color: #010003!important;
    color: #9acc99;
    flex-wrap: wrap;
    top: -1200px;
    z-index: 4;
    position: relative;
    cursor: pointer;
}
.play{
    margin-top: 200px;
    margin-left: 100px;
    display: flex;
    width: 400px;
    height: 200px;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
}
.highscore{
    margin-top: 100px;
    font-size: 30px;
}
.snake-cell{
    display: flex;
    width: 18px;
    height: 18px;
    background-color: #010003;
    border: 1px solid #9acc99;
    z-index: 2;
    position: relative;
}
.empty-cell{
    display: flex;
    width: 18px;
    height: 18px;
    border: 1px solid #9acc99;
    z-index: 2;
    position: relative;
}
.touch{
    display:flex;
    width: 600px;
    height: 600px;
    flex-wrap: wrap;
    z-index: 3;
    position: relative;
}


.board-container{
    display:flex;
    width: 600px;
    height: 600px;
    border: 1px solid black;
    background-color: #9acc99;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
body{
    background-color: #010003;
    color: #9acc99;
    font-family: 'Press Start 2P', cursive;
}

.main-container{
    width: 80%;
    max-width: 100%;
    margin:0 auto;
    align-items: center;
    justify-content: center;
}
.titulo{
    display: flex;
    align-items: center;
    justify-content: center;

}
.content{
    display: flex;
    justify-content: space-around;
}
.containerA{
    height: 640px;
    width: 615px;
    display: flex;
    justify-content: center;
    flex-direction: column;

}
.containerB{
    height: 665px;
    width: 615px;
    display: flex;
    flex-direction: column;
    background-color: #9acc99;
    padding-top: 15px;
    padding-left: 15px;
}
.menu{
    color: #010003;
    margin-top: 20px;
    margin-left: 10px;
    margin-right: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: row;
}
.vidas{
    width: 150px;
    display: flex;
    justify-content: center;
    flex-direction: row;
}
.pausa{
    width: 150px;
    display: flex;
    justify-content: center;
    flex-direction: row;
}
.reset-btn{
    margin-top: -8px;
    text-align: center;
	display: inline-block;
    font-weight: bold;
    background-color: #9acc99;
    padding: 10px 10px 10px 10px ;
    text-shadow: -1px -1px black, 1px 1px white;
    color: #010003;
	border-radius: 7px;
    box-shadow: 0 .2em gray; 
    cursor: pointer;

}
.score{
    width: 150px;
    display: flex;
    justify-content: center;
    flex-direction: row;
}
